<template>

 <div  >
   <mu-appbar class="fixed" :title="book.title">
     <mu-icon-button icon="arrow_back" slot="left" @click="goback()" />
   </mu-appbar>
   <div class="fixed-container">
    <div v-for="slide,index in book.images" :key="index">
     <img v-lazy="slide" lazy="loading">
   </div>
 </div>



 <mu-dialog v-if="!logined" :open="dialog" title="广告">
  <img ="book.images[0]" width="300px" height="auto">
  <mu-flat-button  label="关闭" slot="actions" primary @click="()=>{dialog=false}"/>
  </mu-dialog>
</div>
</template>
<script >
 export default {
  name:'bookComponet',

  mounted(){

  },

  data(){
   return {
     swiperOption: {
      autoplay: 3500,
      setWrapperSize :true,
      centeredSlides: true,
      autoHeight:true,
    },
    dialog:false,
  }
},
computed:{
 book() {
  return this.$store.state.book
},
logined(){
  return this.$store.state.loginedUser
}

},
beforeRouteEnter(to,from,next)
{
  next(vm=>{
    //vm.dialog=!vm.logined
  })
},
methods: {


  goback()
  {
    this.$router.go(-1)
  }


} 
}
</script>
<style scoped>

 .flex{
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
}

.desc-container{
  height:100%;
  overflow-y: scroll;
}
img {
  width: 100%;
}
img[lazy=loading]{
  padding: 30%;
}
.fixed {
  position:fixed;
  top: 0;
}
.fixed-container{
 padding-top: 64px;
 
}

</style>


